import React, { Component } from 'react'

export class MovesList extends Component {
    get moves() {
        let { title, moves } = this.props
        const moveCopy = [...moves]
        const movesToShow = []
        let counter = moves.length >= 3 ? 3 : moves.length
        if (title === 'Your last moves :') {
            while (counter) {
                movesToShow.push(moveCopy.pop())
                counter--
            }
            return movesToShow
        }
        return moves
    }

    render() {
        const { title } = this.props
        const movesToShow = this.moves
        console.log(movesToShow);
        if (!movesToShow.length) {
            return (
                <div>No moves yet..</div>
            )
        } else return (
            <>
                <div>{title}</div>
                {
                    movesToShow?.map(move => <div>at {move.at} to{move.to} amount{move.amount}</div>)
                }
            </>
        )
    }
}
