import { ChartArea } from '@patternfly/react-charts';


function Chart({ chartsDatas }) {

    return (
        <div className="Chart main-layout">
            <h1>Dashboard</h1>
            <ChartArea data={chartsDatas[0].marketPrice.values} />;
            <ChartArea data={chartsDatas[1].confirmedTransactions.values} />;

        </div>
    );
}

export default Chart;
