import { Component } from "react";
import { ContactPreview } from './ContactPreview.jsx'
import { Link } from "react-router-dom"
import { ContactService } from "../services/ContactService.js"



export class ContactList extends Component {


    state = {
        contacts: null
    }

    get contacts() {
        return this.props.contacts
    }

    // async componentDidMount() {
    //     const contacts = await ContactService.getContacts()
    //     this.setState({ contacts })
    // }

    // async componentDidUpdate(prevState) {
    //     const contacts = await ContactService.getContacts()
    //     if (prevState.contacts === contacts) return
    //     this.setState({ contacts })
    // }



    render() {
        // const { contacts } = this.state
        if (!this.contacts) return;

        return (
            <section className="contact-list simple-cards-grid main-layout">
                {
                    this.contacts.map(contact => <ContactPreview key={contact._id} contact={contact} />)
                }
                <Link className="btn add" to={'/contact/edit'}>Create</Link>

            </section>
        );
    }
}