import { UserService } from '../../services/UserService'

export function spendBalance(amount) {
    return async (dispatch) => {
        dispatch({ type: 'SPEND_BALANCE', amount })
    }
}

export function setLoggedInUser(name) {
    return async (dispatch) => {
        try {
            const user = await UserService.signup(name)
            dispatch({ type: 'SET_USER', user })
        } catch (err) {
            console.log('err:', err)
        }
    }
}
export function addMove(contact, amount) {
    return async (dispatch) => {
        try {
            const user = await UserService.addMove(contact, amount)
            dispatch({ type: 'ADD_MOVE', user })
        } catch (err) {
            console.log('err:', err)
        }
    }
}