import { Component } from "react";
import { Link } from "react-router-dom"
import { ContactService } from "../services/ContactService.js"

export class ContactEdit extends Component {
    state = {
        contact: null,
    };

    async componentDidMount() {
        const { id } = this.props.match.params;
        const contact = id ? await ContactService.getContactById(id) : ContactService.getEmptyContact()
        this.setState({ contact, });
    }

    onSaveContact = async (ev) => {
        ev.preventDefault()
        await ContactService.saveContact({ ...this.state.contact })
        this.props.history.push('/')
    }

    handleChange = async ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? (+target.value || '') : target.value
        this.setState(prevState => ({ contact: { ...prevState.contact, [field]: value } }))
    }

    inputRef = (elInput) => {
        if (elInput) elInput.focus()
    }

    delete = async () => {
        await ContactService.deleteContact(this.state.contact.id)
        this.props.history.push('/contact')
    }

    render() {
        const { contact } = this.state;
        if (!contact) return

        return (
            <section className="contact-edit main-layout">
                <h2>{contact._id ? 'Edit' : 'Add'} Contact</h2>
                <form onSubmit={this.onSaveContact} >
                    <label htmlFor="name">Name</label>
                    <input ref={this.inputRef} onChange={this.handleChange} value={contact.name} type="text" name="name" id="name" />
                    <label htmlFor="phone">Phone</label>
                    <input onChange={this.handleChange} value={contact.phone} type="phone" name="phone" id="phone" />
                    <label htmlFor="email">Email</label>
                    <input onChange={this.handleChange} value={contact.email} type="text" name="email" id="email" />
                    <button>Save</button>
                </form>
                {contact._id && <button onClick={() => this.delete()}>Delete</button>}
                <Link to={(contact._id ? `/contact/${contact._id}` : '/contact')}>Back</Link>

            </section >
        );
    }
}