import { Component } from 'react'
import { connect } from 'react-redux'
import { setLoggedInUser } from '../store/actions/userActions'



export class _Signup extends Component {

    state = {
        name: ''
    }

    inputRef = (elInput) => {
        if (elInput) elInput.focus()
    }

    onSaveUser = (ev) => {
        ev.preventDefault()
        this.props.setLoggedInUser(this.state.name)
        this.props.history.push('/')
    }

    handleChange = ({ target }) => {
        this.setState({ name: target.value })
    }

    render() {
        const { name } = this.state
        return (
            <form onSubmit={this.onSaveUser} >
                <label htmlFor="name">Name</label>
                <input ref={this.inputRef} onChange={this.handleChange} value={name} type="text" name="name" id="name" required />
                <button>Register</button>
            </form>
        )
    }
}

// const mapStateToProps = state => {
//     return {
//         user: state.userModule.loggedInUser
//     }
// }

const mapDispatchToProps = {
    setLoggedInUser,
}

// const mapStateToProps = state => {
//     return {
//         user: state.userModule.loggedInUser
//     }
// }

export const Signup = connect(null, mapDispatchToProps)(_Signup)
