import { withRouter } from "react-router-dom";

function _ContactPreview({ contact, history }) {

    function goToDetails() {
        history.push(`/contact/${contact._id}`)
    }

    return (
        <div className="contact-preview" onClick={() => goToDetails(contact._id)}>
            <img className="user-img preview" src={contact.img} alt="" />
            <h1 className="user-name preview">{contact.name}</h1>
        </div>
    );
}

export const ContactPreview = withRouter(_ContactPreview)

