import { Component } from "react";
import { UserService } from "../services/UserService";
import { BitcoinService } from "../services/BitcoinService";
import homeGif from '../assets/scss/imgs/home.gif'
import coins from '../assets/scss/imgs/coins.png'
import { connect } from 'react-redux'
import { MovesList } from '../components/MovesList.jsx'


export class _HomePage extends Component {
    state = {
        user: null,
        bitcoinVal: null,
    };

    async componentDidMount() {
        const { user } = this.props;
        const bitcoinVal = await BitcoinService.getRate(user.coins)
        this.setState({ user, bitcoinVal, });
    }
    render() {
        const { bitcoinVal } = this.state;
        const { user } = this.props;
        console.log(user);
        if (!user) return;


        return (
            <section className="home-page flex align-start main-layout ">
                <section className="flex space-between">
                    <div >
                        <img className="user-img" src={user.img} alt="" />
                    </div>
                    <div >
                        <h1 className="user-name">Hello {user.name} ! </h1>
                        <div className="user-data">
                            <img className="home-img" src={coins} alt="" />
                            <h3>  Credit: ${user.coins}</h3>
                        </div>
                        <div className="user-data">
                            <img src="https://img.icons8.com/external-prettycons-flat-prettycons/47/000000/external-bitcoin-shopping-prettycons-flat-prettycons.png" alt="btc"></img>
                            <h3 className="btc">  BTC: {bitcoinVal}</h3>
                        </div>
                    </div>
                    <div>< MovesList moves={user.moves} title='Your last moves :' /></div>
                </section>
                <div className="main-img" ><img src={homeGif} alt="" /></div>
            </section >
        );
    }
}

const mapStateToProps = state => {
    return {
        user: state.userModule.loggedInUser
    }
}
export const HomePage = connect(mapStateToProps)(_HomePage)