import { UtilService } from '../services/UtilService'
import { StorageService } from './StorageService.js'

export const UserService = {
    getUser,
    signup,
    addMove,
    logout
}

const USER_KEY = 'user_db'

let gUser;


function getUser() {
    let user = gUser
    if (!user) {
        user = StorageService.loadFromStorage(USER_KEY)
    }
    return user
}

function logout() {
    localStorage.clear()
    // StorageService.saveToStorage(USER_KEY, '')
}

function signup(name) {
    if (!name) {
        logout()
        return null
    }
    let user = StorageService.loadFromStorage(USER_KEY)
    if (!user) {
        user = _getEmptyUser()
        user.name = name
    }
    StorageService.saveToStorage(USER_KEY, user)
    gUser = user
    return user
}



function _getEmptyUser() {
    return {
        coins: 100,
        moves: [],
        img: `https://randomuser.me/api/portraits/${Math.random() > 0.5 ? 'men' : 'women'}/${UtilService.getRandomInt(1, 99)}.jpg`
    }
}


function addMove(contact, amount) {
    const user = StorageService.loadFromStorage(USER_KEY)
    const move = {
        at: Date.now(),
        to: contact.name,
        toId: contact._id,
        amount,
        id: UtilService.makeId(),
    }
    user.coins = user.coins - amount
    console.log(amount);
    user.moves.push(move)
    StorageService.saveToStorage(USER_KEY, user)
    return user
}
