import { Component } from "react";
import Chart from '../components/Chart.jsx'
import { BitcoinService } from "../services/BitcoinService.js"

export class StatisticPage extends Component {
    state = {
        chartsDatas: null,
    };

    async componentDidMount() {
        const marketPrice = await BitcoinService.getMarketPrice()
        const confirmedTransactions = await BitcoinService.getConfirmedTransactions()
        this.setState({ chartsDatas: [{ marketPrice }, { confirmedTransactions }] })
    }
    render() {
        const { chartsDatas } = this.state
        if (!chartsDatas) return <div>Loading..</div>

        return (
            <section className="statistic-page">
                <Chart chartsDatas={chartsDatas} />
            </section>
        );
    }
}