import React, { Component } from 'react'
import { connect } from 'react-redux'
import { addMove } from '../store/actions/userActions'

export class _TransferFund extends Component {

    state = {
        amount: ''
    }

    transfer = async () => {
        await this.props.addMove(this.props.contact, this.state.amount)
        this.setState({ amount: null })
        this.props.history.push('/')
    }

    handleChange = ({ target }) => {
        this.setState({ amount: target.value })
    }
    render() {
        const { contact } = this.props
        const { amount } = this.state
        return (
            <>
                < div > Transfer coins to {contact.name} :</div >
                < div>
                    <span> Amount: </span>
                    <form >
                        <input type="number" value={amount} onChange={this.handleChange} />
                    </form >
                    <button onClick={() => this.transfer()}>Transfer</button>
                </div>
            </>)
    }
}

const mapDispatchToProps = {
    addMove,
}

export const TransferFund = connect(null, mapDispatchToProps)(_TransferFund)





