import { NavLink } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { UserService } from '../services/UserService.js'
import logo from '../assets/scss/imgs/logo.svg';
import { connect } from 'react-redux'
import { setLoggedInUser } from '../store/actions/userActions'




function _Header({ history, setLoggedInUser, user }) {
    function onHome() {
        history.push('/')
    }

    function logout() {
        setLoggedInUser()
        history.push('/signup')
    }

    if (!user) {
        return (
            <div className="app-header flex align-center">
                <div className="main-layout flex  align-center">
                    <img className="logo" src={logo} alt="logo"></img>
                    <h1 > Mister Bitcoin </h1>
                </div>
                <div className="main-layout  ">
                    <NavLink activeClassName="my-active" to='/signup'>Sign Up</NavLink>
                </div>
            </div>
        )
    } else return (
        <div className="app-header flex align-center">
            <div className="main-layout flex  align-center">
                <img className="logo" src={logo} alt="logo"></img>
                <h1 className="logo-txt" onClick={() => onHome()}> Mister Bitcoin </h1>
            </div>
            <nav className="main-layout  ">
                <NavLink activeClassName="my-active" exact to='/'>Home</NavLink>
                <NavLink activeClassName="my-active" to='/contact'>Contacts</NavLink>
                <NavLink activeClassName="my-active" to='/statistic'>Statistics</NavLink>
                <span onClick={() => logout()}  >Log out</span>
            </nav>
        </div>
    );
}

const mapStateToProps = state => {
    return {
        user: state.userModule.loggedInUser
    }
}

const mapDispatchToProps = {
    setLoggedInUser,
}

export const Header = connect(mapStateToProps, mapDispatchToProps)(withRouter(_Header))

